/* src/components/Dashboard.css */

/* General Styles */
.dashboard {
  font-family: Arial, sans-serif;
  padding: 20px;
  background-color: #f4f7fa;
  border-radius: 8px;
  box-shadow: 0 2px 8px rgba(0, 0, 0, 0.1);
  max-width: 1200px;
  margin: 0 auto;
}

h2 {
  font-size: 24px;
  font-weight: 600;
  color: #333;
  margin-bottom: 20px;
}

/* Metrics Section */
.metrics {
  display: flex;
  justify-content: space-between;
  margin-bottom: 30px;
}

.metric {
  background-color: #fff;
  border-radius: 8px;
  box-shadow: 0 2px 8px rgba(0, 0, 0, 0.05);
  padding: 20px;
  width: 30%;
  text-align: center;
}

.metric h3 {
  font-size: 20px;
  color: #666;
  margin-bottom: 10px;
}

.metric p {
  font-size: 24px;
  font-weight: 700;
  color: #2d3748;
}

/* Intent Distribution Section */
h3 {
  font-size: 22px;
  font-weight: 600;
  color: #333;
  margin-top: 40px;
  margin-bottom: 15px;
}

ul {
  list-style: none;
  padding: 0;
}

li {
  font-size: 16px;
  color: #555;
  margin-bottom: 8px;
}

li::before {
  content: '•';
  color: #2b6cb0;
  margin-right: 10px;
}

/* Recent Interactions Section */
h3 + ul {
  margin-top: 20px;
}

li {
  font-size: 16px;
  color: #555;
  margin-bottom: 8px;
}

li::before {
  content: '•';
  color: #e53e3e;
  margin-right: 10px;
}

/* Loading and Error States */
.loading {
  font-size: 18px;
  color: #f59e0b;
}

.error {
  font-size: 18px;
  color: #e53e3e;
  font-weight: 600;
}

/* Customer List Styles */
.customer-list {
  display: flex;
  flex-wrap: wrap;
  margin-top: 30px;
}

.customer-item {
  background-color: #fff;
  border-radius: 8px;
  box-shadow: 0 2px 8px rgba(0, 0, 0, 0.05);
  padding: 20px;
  margin: 10px;
  width: 30%;
  cursor: pointer;
  transition: transform 0.3s ease, background-color 0.3s ease;
}

.customer-item:hover {
  transform: scale(1.05);
  background-color: #edf2f7;
}

.customer-item.selected {
  border: 2px solid #3182ce; /* Blue border for selected customer */
  background-color: #e2e8f0; /* Light blue background for selected */
}

.customer-item h4 {
  font-size: 18px;
  color: #333;
  margin-bottom: 10px;
}

.customer-item p {
  font-size: 14px;
  color: #555;
}

/* Customer Interaction Area */
.customer-interaction {
  margin-top: 40px;
  background-color: #fff;
  padding: 20px;
  border-radius: 8px;
  box-shadow: 0 2px 8px rgba(0, 0, 0, 0.05);
}

.customer-interaction h3 {
  font-size: 20px;
  color: #333;
  margin-bottom: 15px;
}

textarea {
  width: 100%;
  height: 150px;
  padding: 10px;
  border-radius: 8px;
  border: 1px solid #ccc;
  font-size: 14px;
  resize: none;
  margin-bottom: 20px;
  font-family: Arial, sans-serif;
}

button {
  padding: 10px 20px;
  background-color: #3182ce;
  color: #fff;
  font-size: 16px;
  border: none;
  border-radius: 8px;
  cursor: pointer;
  transition: background-color 0.3s ease;
}

button:hover {
  background-color: #2b6cb0;
}

button:disabled {
  background-color: #b0c4de;
  cursor: not-allowed;
}

/* Responsive Styles */
@media (max-width: 768px) {
  .metrics {
      flex-direction: column;
      align-items: center;
  }

  .metric {
      width: 80%;
      margin-bottom: 20px;
  }

  ul {
      padding-left: 20px;
  }

  .customer-item {
      width: 80%;
  }

  .customer-interaction {
      width: 100%;
  }
}
